@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.2;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Nunito Sans", sans-serif;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-blue {
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  width: auto;
  height: 49px;
  padding: 0 24px;
  border: 2px solid rgb(79, 143, 240);
  background: rgb(79, 143, 240);
}

.btn-blue:hover {
  border: 2px solid rgb(79, 143, 240);
  color: rgb(79, 143, 240);
  background: rgb(255, 255, 255);
}

.btn-white {
  font-size: 14px;
  font-weight: 900;
  line-height: 17px;
  text-transform: uppercase;
  width: auto;
  height: 49px;
  padding: 16px 24px;
  border: 2px solid rgb(230, 230, 230);
  background: rgb(230, 230, 230);
}

.btn-white:hover {
  border: 2px solid rgb(230, 230, 230);
  background: rgb(255, 255, 255);
}

.btn-container__blue-white {
  display: flex;
  gap: 24px;
}
@media screen and (max-width: 999px) {
  .btn-container__blue-white {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 210px;
  }
}

.block {
  text-align: center;
  padding: 80px 0;
}
@media screen and (max-width: 999px) {
  .block {
    padding-top: 40px;
  }
}
.block-header {
  flex-direction: column;
  display: flex;
  align-items: center;
}
.block-header__title {
  display: inline-block;
  margin-bottom: 13px;
  font-size: 28px;
  font-weight: 800;
  text-transform: uppercase;
  color: black;
}
.block-header__line {
  display: inline-block;
  width: 50px;
  height: 3px;
  margin-bottom: 24px;
  background: rgb(79, 143, 240);
}
.block-header__text {
  max-width: 920px;
  display: inline-block;
  margin-bottom: 48px;
  font-size: 16px;
  font-weight: 400;
}

.header-top {
  padding: 16px 0;
  background-color: black;
  color: white;
}
.header-top__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-top__wrapper-left {
  display: flex;
  justify-content: space-between;
  gap: 24px;
}
.header-top__wrapper-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
}
@media screen and (max-width: 480px) {
  .header-top__wrapper-item--mail {
    display: none;
  }
}
.header-top__wrapper-call {
  font-size: 14px;
  font-weight: 700;
  color: rgb(79, 143, 240);
}

.header-nav {
  padding: 22px 0;
  background: rgba(1, 1, 1, 0.76);
}
.header-nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-nav__wrapper-logo--blue {
  font-size: 28px;
  font-weight: 900;
  text-transform: uppercase;
  color: rgb(79, 143, 240);
}
@media screen and (max-width: 999px) {
  .header-nav__wrapper-logo--blue {
    font-size: 22px;
  }
}
.header-nav__wrapper-logo--white {
  font-size: 20px;
  font-weight: 900;
  text-transform: lowercase;
  color: rgb(255, 255, 255);
}
@media screen and (max-width: 999px) {
  .header-nav__wrapper-logo--white {
    font-size: 16px;
  }
}
.header-nav__btn {
  display: none;
}
@media screen and (max-width: 999px) {
  .header-nav__btn {
    display: block;
    background-color: rgba(1, 1, 1, 0.76);
  }
  .header-nav__btn path {
    fill: #ffffff;
  }
}

.header-nav__menu {
  display: flex;
  gap: 32px;
}
.header-nav__menu-link {
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  color: white;
}
.header-nav__menu-link--blue {
  text-decoration: underline;
  color: rgb(79, 143, 240);
}
@media screen and (max-width: 999px) {
  .header-nav__menu {
    display: none;
  }
}

.header-content {
  background-image: url("/img/header_bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media screen and (max-width: 999px) {
  .header-content {
    background-image: url("/img/bg_for_tablet.png");
  }
}
@media screen and (max-width: 480px) {
  .header-content {
    background-image: url("/img/bg_for_mobile.png");
  }
}
.header-content__info {
  text-align: center;
  padding-top: 150px;
  padding-bottom: 36px;
}
@media screen and (max-width: 999px) {
  .header-content__info {
    padding: 60px 0 24px 0;
  }
}
@media screen and (max-width: 480px) {
  .header-content__info {
    padding: 50px 0 50px 0;
  }
}
.header-content__wrapper {
  flex-direction: column;
  display: flex;
  align-items: center;
}
.header-content__wrapper-title--white {
  display: inline-block;
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 8px;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
@media screen and (max-width: 999px) {
  .header-content__wrapper-title--white {
    font-size: 24px;
  }
}
@media screen and (max-width: 480px) {
  .header-content__wrapper-title--white {
    font-size: 18px;
  }
}
.header-content__wrapper-title--blue {
  display: inline-block;
  font-size: 56px;
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
  color: rgb(79, 143, 240);
}
@media screen and (max-width: 999px) {
  .header-content__wrapper-title--blue {
    font-size: 50px;
  }
}
@media screen and (max-width: 480px) {
  .header-content__wrapper-title--blue {
    font-size: 30px;
  }
}
.header-content__wrapper-text {
  max-width: 1090px;
  display: inline-block;
  font-size: 20px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 48px;
  color: rgb(255, 255, 255);
}
@media screen and (max-width: 999px) {
  .header-content__wrapper-text {
    font-size: 18px;
    margin-bottom: 46px;
  }
}
@media screen and (max-width: 480px) {
  .header-content__wrapper-text {
    font-size: 16px;
    margin-bottom: 32px;
  }
}
.header-content__btn {
  margin-bottom: 120px;
}
@media screen and (max-width: 999px) {
  .header-content__btn {
    margin-bottom: 36px;
  }
}

.header-nav__slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 480px) {
  .header-nav__slider {
    display: none;
  }
}

.header-nav__wrap-line {
  display: flex;
  align-items: flex-end;
  gap: 8px;
}

.header-nav__line-item {
  width: 4px;
  height: 24px;
  background: rgb(230, 230, 230);
}
.header-nav__line-item--blue {
  height: 40px;
  background: rgb(79, 143, 240);
}

.services {
