@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900&display=swap');

body {
    font-family: $font-text;
}

.container {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 15px;

}

.btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-blue {
    font-size: 14px;
    font-weight: 900;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 1);

    width: auto;
    height: 49px;
    padding: 0 24px;
    border: 2px solid rgba(79, 143, 240, 1);
    background: rgba(79, 143, 240, 1);
}

.btn-blue:hover {
    border: 2px solid rgba(79, 143, 240, 1);
    color: rgba(79, 143, 240, 1);
    background: rgba(255, 255, 255, 1);

}

.btn-white {
    font-size: 14px;
    font-weight: 900;
    line-height: 17px;
    text-transform: uppercase;

    width: auto;
    height: 49px;
    padding: 16px 24px;
    border: 2px solid rgba(230, 230, 230, 1);
    background: rgba(230, 230, 230, 1);

}

.btn-white:hover {
    border: 2px solid rgba(230, 230, 230, 1);
    background: rgba(255, 255, 255, 1);
}

.btn-container__blue-white {
    display: flex;
    gap: 24px;

    @include mediaTablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 210px;
    }
}

.block {
    text-align: center;
    padding: 80px 0;

    @include mediaTablet {
        padding-top: 40px;
    }

    &-header {
        flex-direction: column;
        display: flex;
        align-items: center;

        &__title {
            display: inline-block;
            margin-bottom: 13px;
            font-size: 28px;
            font-weight: 800;
            text-transform: uppercase;
            color: black;
        }

        &__line {
            display: inline-block;
            width: 50px;
            height: 3px;
            margin-bottom: 24px;
            background: rgba(79, 143, 240, 1);
        }

        &__text {
            max-width: 920px;
            display: inline-block;
            margin-bottom: 48px;
            font-size: 16px;
            font-weight: 400;
        }
    }
}